.template-homepage .pass-input.copy-confirmation {
  background-color: #a6deb3 !important;
  transition: 0.3s;
}
.template-homepage .pass-name {
  border: none;
}
.template-homepage #settings_toggle .settings_btn_label.hide {
  display: none;
}
.template-homepage #settings_toggle[aria-expanded=true] .settings_btn_label.show {
  display: none;
}
.template-homepage #settings_toggle[aria-expanded=true] .settings_btn_label.hide {
  display: block;
}
@media only screen and (max-width: 720px) {
  .template-homepage h1 {
    font-size: 2rem;
  }
  .template-homepage p.lead {
    font-size: 1rem;
  }
  .template-homepage button.btn {
    width: 100%;
  }
  .template-homepage #pass_copy {
    margin-top: 15px;
  }
  .template-homepage .ratings {
    margin-bottom: 15px;
    min-height: 18px;
  }
  .template-homepage #history input.form-control {
    margin-right: 15px;
  }
  .template-homepage #history .history-copy {
    width: auto;
  }
  .template-homepage .custom-switch {
    padding-bottom: 1rem;
    padding-left: 2.25rem;
  }
  .template-homepage .custom-switch .custom-control-label {
    padding-left: 2rem;
    padding-top: 0.5rem;
  }
  .template-homepage .custom-switch .custom-control-label::before {
    border-radius: 1rem;
    height: 2rem;
    width: 3.5rem;
  }
  .template-homepage .custom-switch .custom-control-label::after {
    border-radius: 2rem;
    height: calc(2rem - 4px);
    width: calc(2rem - 4px);
  }
  .template-homepage .custom-switch .custom-control-input:checked ~ .custom-control-label::after {
    transform: translateX(1.5rem);
  }
}