.template-homepage {
	.pass-input {
		&.copy-confirmation {
			background-color: #a6deb3 !important;
			transition: .3s;
		}
	}
	
	.pass-name {
		border: none;
	}
	
	#settings_toggle {
		.settings_btn_label {
			&.hide {
				display: none;
			}	
		}
		
		&[aria-expanded="true"] {
			.settings_btn_label {
				&.show {
					display: none;
				}
				
				&.hide {
					display: block;
				}	
			}
		}
	}
	
	@media only screen and (max-width: 720px) {
	    h1 {
	    	font-size: 2rem;
	    }
	
	    p.lead {
	    	font-size: 1rem;
	    }
	
	    button.btn {
	    	width: 100%;
	    }
	
	    #pass_copy {
	    	margin-top: 15px;
	    }
	
	    .ratings {
			margin-bottom: 15px;
	    	min-height: 18px;
	    }
	
	    #history {
			input.form-control {
				margin-right: 15px;
			}
			
			.history-copy {
				width: auto;
			}
	    }
		
	    .custom-switch {
			padding-bottom: 1rem;
			padding-left: 2.25rem;
			
			& .custom-control-label {
				padding-left: 2rem;
				padding-top: 0.5rem;
			
				&::before {
					border-radius: 1rem;
					height: 2rem;
					width: 3.5rem;
				}
				
				&::after {
					border-radius: 2rem;
					height: calc(2rem - 4px);
					width: calc(2rem - 4px);
				}
			}
			
			.custom-control-input:checked ~ .custom-control-label::after {
				transform: translateX(1.5rem);
			}
		}
	}
}
